'use client'

import { useState, useEffect } from 'react'
import Tabs from '@/components/ui/Tabs'
import UserTable from '@/components/users/UserTable'
import UserForm from '@/components/users/UserForm'
import { Users as UsersIcon, Search } from 'lucide-react'

type User = {
  id: number
  username: string
  name: string | null
  email: string | null
  nickname: string | null
  phone: string | null
  mobile: string | null
  title: string | null
  role: string | null
  active: number | null
  roles?: string[]
}

export default function UsersPage() {
  const [users, setUsers] = useState<User[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [viewingUsers, setViewingUsers] = useState<User[]>([])
  const [activeTab, setActiveTab] = useState('all')
  const [searchTerm, setSearchTerm] = useState('')

  useEffect(() => {
    fetchUsers()
  }, [])

  const fetchUsers = async () => {
    try {
      setLoading(true)
      setError(null)
      
      const res = await fetch('/api/users')
      
      if (!res.ok) {
        throw new Error(`Failed to fetch users: ${res.status}`)
      }
      
      const data = await res.json()
      setUsers(data)
    } catch (error) {
      console.error('Error fetching users:', error)
      setError(error instanceof Error ? error.message : 'Failed to load users')
    } finally {
      setLoading(false)
    }
  }

  const handleView = (user: User) => {
    const alreadyViewing = viewingUsers.find(u => u.id === user.id)
    if (!alreadyViewing) {
      setViewingUsers(prev => [...prev, user])
    }
    setActiveTab(`view-${user.id}`)
  }

  const handleCloseViewTab = (userId: number) => {
    setViewingUsers(prev => prev.filter(u => u.id !== userId))
    setActiveTab('all')
  }

  const filteredUsers = users.filter(user =>
    user.name?.toLowerCase().includes(searchTerm.toLowerCase()) ||
    user.username.toLowerCase().includes(searchTerm.toLowerCase()) ||
    user.email?.toLowerCase().includes(searchTerm.toLowerCase()) ||
    user.title?.toLowerCase().includes(searchTerm.toLowerCase())
  )

  if (loading) {
    return (
      <div className="p-6">
        <div className="animate-pulse">
          <div className="h-8 bg-slate-200 rounded w-1/4 mb-4"></div>
          <div className="h-64 bg-slate-200 rounded"></div>
        </div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="p-6">
        <div className="bg-red-50 border border-red-200 text-red-700 px-4 py-3 rounded">
          <p className="font-semibold">Error loading users</p>
          <p className="text-sm">{error}</p>
          <button 
            onClick={fetchUsers}
            className="mt-2 px-4 py-2 bg-red-600 text-white rounded hover:bg-red-700"
          >
            Retry
          </button>
        </div>
      </div>
    )
  }

  const userListTab = (
    <div>
      {/* Info Banner */}
      <div className="mb-4 p-4 bg-blue-50 border border-blue-200 rounded-lg">
        <p className="text-sm text-blue-800">
          <strong>Note:</strong> This is a read-only directory. To manage users, assign roles, or reset passwords, 
          please contact an administrator or access the Admin → User Management section if you have permission.
        </p>
      </div>

      {/* Search Bar */}
      <div className="mb-4">
        <div className="relative">
          <Search className="absolute left-3 top-1/2 -translate-y-1/2 text-slate-400" size={20} />
          <input
            type="text"
            placeholder="Search users by name, username, email, or title..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="w-full pl-10 pr-4 py-2 border border-slate-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500 outline-none"
          />
        </div>
      </div>

      {/* Users Count */}
      <div className="mb-4">
        <p className="text-sm text-slate-600">
          Showing <span className="font-semibold">{filteredUsers.length}</span> of <span className="font-semibold">{users.length}</span> users
          {searchTerm && <span> (filtered)</span>}
        </p>
      </div>

      {/* User Table */}
      <UserTable 
        users={filteredUsers} 
        onView={handleView}
        showActions={true}
      />
    </div>
  )

  const tabs = [
    { 
      id: 'all', 
      label: 'All Users', 
      content: userListTab,
      closeable: false
    },
    ...viewingUsers.map(user => ({
      id: `view-${user.id}`,
      label: user.name || user.username,
      content: (
        <UserForm
          user={user}
          mode="view"
          onClose={() => handleCloseViewTab(user.id)}
        />
      ),
      closeable: true,
      onClose: () => handleCloseViewTab(user.id)
    }))
  ]

  return (
    <div className="p-6">
      <div className="mb-6">
        <h2 className="text-2xl font-bold text-slate-800 flex items-center gap-2">
          <UsersIcon size={28} />
          Users Directory
        </h2>
        <p className="text-slate-600 mt-1">View system users (read-only)</p>
      </div>

      <div className="bg-white rounded-lg shadow">
        <div className="p-6">
          <Tabs tabs={tabs} activeTab={activeTab} onTabChange={setActiveTab} />
        </div>
      </div>
    </div>
  )
}